import React from 'react';
import { render, fireEvent, act, waitFor } from '@testing-library/react-native';
import { Animated, ScrollView } from 'react-native';
import '@testing-library/jest-native';

import CategorySelector from '../../../src/components/exercise-common/CategorySelector';

jest.useFakeTimers();

// --- MOCK Animated.timing ---
jest.spyOn(Animated, 'timing').mockImplementation(() => ({
  start: (callback) => {
    setTimeout(() => {
      act(() => callback?.({ finished: true }));
    }, 500);
  },
  stop: jest.fn(),
}));

// --- MOCK ScrollView ---
// La correction est ici : scrollToSpy est déclarée DANS la fonction de mock.
jest.mock('react-native/Libraries/Components/ScrollView/ScrollView', () => {
  const React = require('react');
  const scrollToSpy = jest.fn(); // Déclaration déplacée ici

  const MockScrollView = React.forwardRef((props, ref) => {
    React.useImperativeHandle(ref, () => ({ scrollTo: scrollToSpy }));

    const childrenWithLayouts = React.Children.map(props.children, (child, index) => {
      if (!child) return null;
      return React.cloneElement(child, {
        onLayout: () =>
          child.props.onLayout?.({
            nativeEvent: { layout: { x: index * 100, y: 0, width: 80, height: 40 } },
          }),
      });
    });

    return <>{childrenWithLayouts}</>;
  });

  MockScrollView.scrollToSpy = scrollToSpy;
  return MockScrollView;
});

describe('CategorySelector', () => {
  const mockCategories = [
    { id: '1', name: 'Fruits' },
    { id: '2', name: 'Vegetables' },
    { id: '3', name: 'Dairy Products' },
    { id: '4', name: 'Meat & Fish' },
    { id: '5', name: 'Beverages' },
  ];

  const stableProps = {
    categories: mockCategories,
    selectedCategory: '1',
    onSelectCategory: jest.fn(),
  };

  const renderSelector = (props = {}) =>
    render(<CategorySelector {...stableProps} {...props} />);

  beforeEach(() => {
    jest.clearAllMocks();
    jest.useFakeTimers();
    ScrollView.scrollToSpy.mockClear();
  });

  afterEach(() => {
    jest.useRealTimers();
  });

  // --- RENDERING ---
  it('renders correctly with categories', () => {
    const { getByText } = renderSelector();
    mockCategories.forEach(cat => expect(getByText(cat.name)).toBeTruthy());
  });

  it('highlights selected category', () => {
    const { getByRole } = renderSelector({ selectedCategory: '2' });
    expect(getByRole('button', { name: 'Vegetables' })).toHaveAccessibilityState({ selected: true });
  });

  it('handles empty categories gracefully', () => {
    const { queryByText } = renderSelector({ categories: [] });
    mockCategories.forEach(cat => expect(queryByText(cat.name)).toBeNull());
  });

  // --- USER INTERACTIONS ---
  it('calls onSelectCategory on different category press', () => {
    const onSelect = jest.fn();
    const { getByText } = renderSelector({ selectedCategory: '1', onSelectCategory: onSelect });
    fireEvent.press(getByText('Vegetables'));
    expect(onSelect).toHaveBeenCalledWith('2');
  });

  it('does not call onSelectCategory when pressing same category', () => {
    const onSelect = jest.fn();
    const { getByText } = renderSelector({ selectedCategory: '1', onSelectCategory: onSelect });
    fireEvent.press(getByText('Fruits'));
    expect(onSelect).not.toHaveBeenCalled();
  });

  // --- ANIMATIONS ---
  it('triggers press animations safely', () => {
    const { getByText } = renderSelector();
    const pill = getByText('Vegetables');
    act(() => {
      fireEvent(pill, 'pressIn');
      fireEvent(pill, 'pressOut');
    });
    expect(pill).toBeTruthy();
  });

  it('prevents overlapping animations', () => {
    const onSelect = jest.fn();
    const { getByText } = renderSelector({ selectedCategory: '1', onSelectCategory: onSelect });
    fireEvent.press(getByText('Vegetables'));
    act(() => jest.runAllTimers());
    fireEvent.press(getByText('Dairy Products'));
    expect(onSelect).toHaveBeenCalledTimes(1);
  });

  // --- SCROLLING ---
  it('scrolls to selected category on mount', async () => {
    renderSelector({ selectedCategory: '4' });
    act(() => jest.runAllTimers());
    await waitFor(() => expect(ScrollView.scrollToSpy).toHaveBeenCalled());
  });

  it('scrolls when selectedCategory changes', async () => {
    const { rerender } = renderSelector({ selectedCategory: '1' });
    act(() => {
      rerender(<CategorySelector {...stableProps} selectedCategory="5" />);
      jest.runAllTimers();
    });
    await waitFor(() => expect(ScrollView.scrollToSpy).toHaveBeenCalledTimes(1));
  });

  // --- EDGE CASES ---
  it('handles undefined, null, or invalid selectedCategory', () => {
    [undefined, null, 'non-existent'].forEach(sel => {
      const { getByText } = renderSelector({ selectedCategory: sel });
      mockCategories.forEach(cat => expect(getByText(cat.name)).toBeTruthy());
    });
  });

  it('handles missing onSelectCategory prop', () => {
    const { getByText } = renderSelector({ onSelectCategory: undefined });
    expect(() => fireEvent.press(getByText('Vegetables'))).not.toThrow();
  });

  it('renders categories with special or long names', () => {
    const special = [
      { id: '1', name: 'Café & Thé' },
      { id: '2', name: 'Fruits & Légumes' },
      { id: '3', name: 'Viande/Poisson' },
      { id: '4', name: 'This is a very long category name that might cause layout issues' },
    ];
    const { getByText } = renderSelector({ categories: special });
    special.forEach(cat => expect(getByText(cat.name)).toBeTruthy());
  });

  // --- PERFORMANCE ---
  it('handles large number of categories efficiently', () => {
    const many = Array.from({ length: 100 }, (_, i) => ({ id: `${i + 1}`, name: `Category ${i + 1}` }));
    const start = Date.now();
    const { queryAllByText } = renderSelector({ categories: many });
    const end = Date.now();
    expect(end - start).toBeLessThan(1000);
    expect(queryAllByText(/^Category/)).toHaveLength(100);
  });

  // --- ACCESSIBILITY ---
  it('provides correct accessibility labels', () => {
    const { getByLabelText } = renderSelector();
    mockCategories.forEach(cat => expect(getByLabelText(cat.name)).toBeTruthy());
  });

  it('supports screen reader navigation', () => {
    const { getAllByRole } = renderSelector();
    expect(getAllByRole('button')).toHaveLength(mockCategories.length);
  });
});